  .psv-tooltip--bottom-left {
    box-shadow: -3px 3px 0 rgba(90, 90, 90, 0.7);
    transform: translate3d(0, -5px, 0);
    transition-property: opacity, transform; }
    .psv-tooltip--bottom-left .psv-tooltip-arrow {
